.commentAvatarImage{
  position: relative;
  left: 17px;
  width:70px;    
  height:70px;    
  line-height: 0;		/* remove line-height */     
  display: inline-block;	/* circle wraps image */   
  border-radius: 50%;	/* relative value */   
  -moz-border-radius: 50%;   
  -webkit-border-radius: 50%;   
  -webkit-transition:linear 0.25s;   
  transition: linear 0.25s;
}
.UserName{
  position: relative;
    /* font-size: 1.14em; */
  color: #fff;
  bottom: 65px;
  left: 100px;
}
.Describe{
  position: absolute;
  top: 90px;
  left: 100px;
  color: #fff;
}
.VideoText{
  color: #fff;
  margin-top: 4%
}
.BottomDiv {
  position: absolute;
  bottom: 0px;
  height: 9.8%;
  width: 100%;
  background:rgba(255,255,255,0.1) none repeat scroll !important;
}
.BabyDiary {
  color: #fff;
  position: absolute;
  bottom: 50%;
  left: 19%;
}
.BabyDiaryText{
  color: #fff;
  position: absolute;
    left: 19%;
    bottom: 16%;
  opacity: 0.5;
  font-size: 0.9em;
}
.OpenDownload{
  position: absolute;
  width: 71px;
  height: 34px;
  left: 77%;
  bottom: 17%;
  z-index: 0;
}
.Video{
    /* width: 400px; */
  height: 250px;
}
.VideoDescription{
  margin-top:2%;
  color: #fff;
}
.vjs-custom-skin{
  height: 38%;
}
.BottomLabel {
  position: absolute;
  bottom: 28%;
  color: #fff;
  left: 80%;
  z-index: 1;
}
.video-js {
  position: absolute;
  font-size: 6px;
}
.vjs_video_3-dimensions {
    height: 100%;
}
